import Link from "next/link";
import { getAllTreatments } from "../../src/lib/data/treatments";

export const metadata = {
  title: "Treatments",
  description:
    "Browse all HEALO treatments and compare partner hospitals in Korea.",
};

export default async function TreatmentsPage() {
  const treatments = await getAllTreatments();

  return (
    <main className="max-w-6xl mx-auto px-4 py-10 space-y-6">
      <div className="space-y-2">
        <h1 className="text-2xl font-bold text-gray-900">All Treatments</h1>
        <p className="text-gray-600">
          Explore every treatment and request a personalized quote.
        </p>
      </div>
      <div className="grid gap-4 md:grid-cols-3">
        {treatments.map((treatment) => (
          <Link
            key={treatment.id}
            href={`/treatments/${treatment.id}`}
            className="bg-white rounded-2xl border border-gray-100 p-4 hover:shadow-md transition-shadow"
          >
            <h2 className="text-lg font-semibold text-gray-900">
              {treatment.title}
            </h2>
            <p className="text-sm text-gray-500 mt-1 line-clamp-3">
              {treatment.desc || treatment.fullDescription}
            </p>
            <div className="text-sm text-gray-700 mt-3">
              {treatment.hospitalName}
            </div>
          </Link>
        ))}
        {treatments.length === 0 && (
          <p className="text-sm text-gray-500">No treatments available.</p>
        )}
      </div>
    </main>
  );
}
